import Vue from 'vue'
import Vuex, { Store } from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    backend_url: "http://beyondf1:80/api/v1",
    load: false,
  },
  mutations: {
    toggleLoad(state){
      state.load = !state.load;
    }
  },
  actions: {
    
  },
  modules: {
  }
})
